import React from "react";
import styled from "styled-components";

const StyledDiv = styled.div`
    background-color: blue;
    width: 100%;
    margin-top: 0px;
    height: 5rem;
`;

const StyledEmail = styled.h2`
    float: right;
    display: inline-block;
    margin-top: 0px;
    color: white;
    padding-top: 2rem;
    padding-right: 2rem;
`;

function Contact(){
    return (
        <StyledDiv>
            <StyledEmail>Contact : chomyungha@gmail.com</StyledEmail>
        </StyledDiv>
  );
}

export default Contact;