import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

const StyledDiv = styled.div`
    background-color: blue;
    width: 100%;
    height: 5rem;
`;

const StyledNavbar = styled.nav`
    float: right;
`;

const StyledTitle = styled(Link)`
    float: left;
    color: white;
    padding-top: 2rem;
    padding-left: 2rem;
    text-decoration: none;
    font-size: 1rem;
    font-familiy: serif;
    font-weight: bold;
`;

const StyledNav = styled(Link)`
    float: right;
    color: white;
    margin-right: 2rem;
    padding-top: 2rem;
    text-decoration: none;
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
`;

function Navigation(){
    return (
        <StyledDiv>
            <StyledTitle to="/">MyungHa Cho</StyledTitle>
            <StyledNavbar >
                <StyledNav to="/contact">Contact</StyledNav>
                <StyledNav to="/capabilities">Capabilities</StyledNav>
                <StyledNav to="/works">Works</StyledNav>
            </StyledNavbar>
        </StyledDiv>
  );
}

export default Navigation;