import React from "react";
import styled from "styled-components";

const StyledDiv = styled.div`
    margin-top: 1rem;
    margin-bottom: 0px;
    background-color: black;
    width: 100%;
    height: 40rem;
`;

const StyledMain = styled.h1`
    color: white;
    margin-top: 0px;
    padding-top: 1rem;
    padding-left: 3rem;
    font-size: 3rem;
    text-align: left;
    font-familiy: Fantasy;
`;

function Works(){
    return (
        <StyledDiv>
            <StyledMain>My works</StyledMain>
        </StyledDiv>
  );
}

export default Works;