import React from "react";
import styled from "styled-components";

const StyledDiv = styled.div`
    background-color: black;
    width: 100%;
    height: 40rem;
    margin-top: 0px;
`;

const StyledMain = styled.h1`
    color: white;
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 12rem;
    padding-left: 3rem;
    font-size: 3rem;
    text-align: left;
    font-familiy: Fantasy;
`;

const StyledSub = styled.h2`
    color: blue;
    padding-left: 3rem;
    margin-bottom: 1rem;
    font-size: 2rem;
`;

const StyledComment = styled.h4`
    color: white;
    margin-top: 0px;
    margin-bottom: 1rem;
    padding-left: 3rem;
    font-size: 1rem;
`;


function Home(){
    return (
        <StyledDiv>
            <StyledMain>I'm ready to design your website and build it</StyledMain>
            <StyledComment>Reference: https://cagdasunal.com/#contact</StyledComment>
            <StyledSub>Check out my work!</StyledSub>
        </StyledDiv>
  );
}

export default Home;