import React from "react";
import styled from "styled-components";

const StyledDiv = styled.div`
    margin-top: 1rem;
    margin-bottom: 0px;
    background-color: black;
    width: 100%;
    height: 40rem;
`;

const StyledMain = styled.h1`
    width: 60%;
    color: white;
    margin-top: 0px;
    padding-top: 1rem;
    padding-left: 3rem;
    font-size: 3rem;
    text-align: left;
    font-familiy: Fantasy;
`;

const StyledSub = styled.h2`
    color: blue;
    margin-top: 0px;
    padding-left: 3rem;
    font-size: 2rem;
`;

const StyledComment = styled.h4`
    color: white;
    margin-top: 0px;
    padding-left: 3rem;
    font-size: 1rem;
`;

function Capabilities(){
    return (
        <StyledDiv>
            <StyledMain>Capabilities</StyledMain>
            <StyledSub>Programming Languages</StyledSub>
            <StyledComment>C, C++, C#</StyledComment>
            <StyledComment>Python, Java</StyledComment>
            <StyledComment>R</StyledComment>
            <StyledComment>Solitidy</StyledComment>           
            <StyledComment>Javascript</StyledComment>
            <StyledSub>Interests</StyledSub>
            <StyledComment>BlockChain</StyledComment>
            <StyledComment>AI</StyledComment>
            <StyledComment>Full-stack Development</StyledComment>
        </StyledDiv>
        
  );
}

export default Capabilities;